	position: relative;
	overflow: hidden;
	max-width: 100%;
	max-height: 100%;
}

.carousel__track {
	display: flex;
}

.carousel__slide {
	flex: 0 0 auto;
	width: var(--carousel-slide-width, 60%);
	max-width: 100%;
	padding: 1rem;
	position: relative;
	overflow-x: hidden;
	overflow-y: auto;
