	height: 8px;
	border-radius: 50%;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-color: currentColor;
	opacity: 0.25;
	transition: opacity 0.15s ease-in-out;
