html {
	font-size: 10px;
}

body {
	margin: 0;
	min-width: 320px;
	background: url("../images/background.jpg") repeat;
	color: #8c8983;
	font: 400 1.6rem/1.3 "Rubik", sans-serif;
	font-smoothing: antialiased;
	text-rendering: optimizeLegibility;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	position: relative;
	display: flex;
	flex-direction: column;
}
body.buddypress #page #primary {
	max-width: 125rem;
}

a {
	text-decoration: none;
	color: inherit;
	transition: all 0.3s ease;
}

a:hover {
	color: inherit;
	text-decoration: underline;
}

input:-webkit-autofill {
	box-shadow: 0 0 0 1000px #4d4d4d inset !important;
}
input:focus, input:focus-visible {
	outline: 0;
	box-shadow: none;
}

button,
input[type=search],
input[type=button],
input[type=reset],
input[type=submit] {
	-webkit-appearance: none;
	cursor: pointer;
	transition: all 0.3s ease;
}

button::-moz-focus-inner,
input[type=reset]::-moz-focus-inner,
input[type=button]::-moz-focus-inner,
input[type=submit]::-moz-focus-inner {
	border: none;
	padding: 0;
}

.wrapper {
	position: relative;
	min-height: 100vh;
	min-height: calc(var(--vh, 1vh) * 100);
	display: flex;
	flex-direction: column;
}

.site-main {
	position: relative;
	z-index: 5;
	flex-grow: 1;
	max-width: 125rem;
	width: 100%;
	margin: auto;
	padding-top: 1.5rem;
	background: #181512;
	border: solid 0.1rem rgba(242, 232, 217, 0.2);
}

h1, .h1,
h2,
.h2,
h3,
.h3,
h4,
.h4 {
	color: #f2e8d9;
}

h1, .h1 {
	font-family: "Oswald", sans-serif;
	margin-bottom: 1rem;
	font-size: 2.6rem;
	font-weight: 400;
	color: #e2e2e2;
}

h2, .h2 {
	margin: 0 0 1.5rem 0;
	font-family: "Oswald", sans-serif;
	font-weight: 400;
	font-size: 2rem;
	color: #e2e2e2;
	text-transform: uppercase;
	border-bottom: 0.1rem solid #202020;
	padding-bottom: 1.5rem;
}

h3,
.h3 {
	font-family: "Oswald", sans-serif;
	margin-bottom: 1.5rem;
	font-size: 1.8rem;
	font-weight: 400;
	color: #e2e2e2;
}

strong,
b {
	font-weight: 600;
}

.bi {
	line-height: 0;
}

hr {
	border: none;
	margin: 0;
	opacity: 1;
	border-bottom: solid 0.1rem rgba(242, 232, 217, 0.2);
}

.tooltip-inner {
	padding: 0.5rem;
}

.bg-img {
	background-position: 50% 50%;
	background-repeat: no-repeat;
	background-size: cover;
}

.sr-only {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	white-space: nowrap;
	border: 0;
}

.meta-info {
	padding: 1.5rem 0 0 0;
	margin: 0;
	list-style: none;
	display: flex;
	flex-wrap: wrap;
	font-size: 1.3rem;
	color: #9b9a97;
	border-top: 0.1rem solid rgba(242, 232, 217, 0.2);
}
.meta-info li {
	display: flex;
	align-items: center;
	margin-bottom: 0.5rem;
	margin-right: 3rem;
	line-height: 1;
}
@media (max-width: 767.98px) {
	.meta-info li {
		margin-bottom: 1rem;
	}
}
.meta-info li .icon-holder {
	margin-right: 0.5rem;
}

.informer-row-border {
	border-right: 0.1rem solid rgba(242, 232, 217, 0.2);
}

.fancybox__container {
	z-index: 1000000;
}

.bg-site {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.hide-reviews-plugin #xs_review_form_public_data,
.hide-reviews-plugin .view-review-list {
	display: none;
}

.single-page {
	color: #afaeac;
}

.comment-rating-form {
	display: flex;
	margin-bottom: 1rem;
}
.comment-rating-form.complete {
	pointer-events: none;
}

