.top-mods {
  margin: 0;
  padding: 0;
  counter-reset: section;
  list-style: none;

  .mods-item {
    position: relative;
    display: flex;
    margin-bottom: 1.5rem;
    border-bottom: 0.1rem solid $tuatara;
    padding-bottom: 1.5rem;

    @include media-breakpoint-down(md) {
      flex-direction: column;
    }

    .img-holder {
      min-width: 25rem;

      @include media-breakpoint-up(md) {
        width: 25rem;
        height: 15rem;
        margin-right: 1.5rem;
      }

      @include media-breakpoint-down(md) {
        margin-bottom: 1.5rem;
      }

      img {
        width: 100%;
        height: auto;
        border-radius: 0.5rem;

        @include media-breakpoint-up(md) {
          height: 100%;
          object-fit: cover;
        }
      }
    }

    h2 {
      padding-bottom: 0;
      border-bottom: none;
    }

    &::before {
      counter-increment: section;
      content: counter(section);
      margin-right: 1.5rem;
      font-weight: 600;
      font-size: 2rem;
      font-family: $secondary-font;
      min-width: 2.8rem;

      @include media-breakpoint-down(md) {
        margin-bottom: 1.5rem;
        font-size: 1.5rem;
      }
    }

    .item-meta {
      margin: 0;
      padding: 0;
      list-style: none;
      display: flex;

      li {
        display: flex;
        align-items: center;
        line-height: 1;

        .icon-holder {
          display: flex;
          margin-right: 0.5rem;
        }

        &:not(:last-child) {
          margin-right: 2rem;
        }
      }
    }

    .item-votes {
      margin-left: 0.2rem;
      vertical-align: sub;
      font-size: smaller;
      align-self: flex-end;
      opacity: 0.9;
      font-style: italic;
    }
  }

  &.style2 {
    .mods-item {
      flex-direction: column;

      .h2 {
        text-transform: none;
      }

      .img-holder {
        width: 100%;
        margin-right: 0;
        margin-bottom: 1.5rem;
      }

      &::before {
        display: none;
      }
    }
  }
}
