.wpft-profile {
	.wpforo-right-sidebar {
		display: none !important;
	}
}

#wpforo {
	width: unset !important;

	#wpforo-wrap {
		background: unset !important;

		&.wpf-dark {
			margin: 0 !important;
			padding: 0 !important;
		}

		.wpforo-right-sidebar {
			width: 100% !important;
			display: flex;
			flex-wrap: wrap;

			@include media-breakpoint-down(sm) {
				display: none;
			}

			@include media-breakpoint-up(sm) {
				gap: 1.5rem;
			}

			aside {
				width: calc(25% - 1.2rem) !important;

				@include media-breakpoint-down(md) {
					width: calc(50% - 0.75rem) !important;
				}

				@include media-breakpoint-down(sm) {
					width: 100% !important;
				}
			}

			.wpforo-widget-wrap {
				padding: 0 !important;
			}

			.widget-title {
				border-bottom: 2px solid #727272 !important;
			}
		}

		.wpforo-list-item-right {
			float: unset !important;

			.postuser {
				font-size: 1.2rem;
				line-height: 1.4rem;
			}

			.posttitle {
				display: -webkit-box;
				-webkit-box-orient: vertical;
				overflow: hidden;
				text-overflow: ellipsis;
				-webkit-line-clamp: 1;
			}
		}

		.wpforo-forum-icon {
			border-left: 0 !important;
		}

		[wpf-tooltip]::after {
			@include media-breakpoint-down(xl) {
				left: unset !important;
				right: 0;
				transform: translateY(-100%) translateX(0) !important;
			}
		}

		.wpf-res-menu {
			color: #999999 !important;
		}

		.wpforo-main {
			display: flex;
			flex-direction: column-reverse;

			.wpforo-content {
				width: 100% !important;
			}

			.wpforo-profile-content {
				width: 100% !important;
			}
		}

		#wpforo-menu {
			background: #282628 !important;
		}

		.wpfl-1 {
			.wpforo-category {
				background: #282628 !important;
				border-radius: 0 !important;
			}

			.wpforo-subforum {
				border-top: #323232 1px dashed !important;
			}
		}

		.wpf-search {
			i {
				pointer-events: none;
				z-index: 2 !important;
			}

			input[type="text"] {
				&.wpf-search-field {
					background: #282628 !important;
					z-index: 1 !important;
				}
			}
		}

		.wpf-notifications {
			background: #282628 !important;
			border-bottom: none !important;

			.wpf-notification-head,
			.wpf-notification-content {
				color: inherit !important;
			}
		}

		.wpf-breadcrumb {
			a {
				color: inherit !important;
			}
		}

		.wpforo-post-head {
			.wpf-post-head-top {
				.wpf-post-info {
					.wpf-post-info-forum {
						color: inherit !important;
					}
				}

				.wpf-post-stat {
					.wpf-post-stat-box {
						span {
							color: inherit !important;
						}
					}
				}
			}
		}

		.wpforo-post-content {
			display: flex;
			flex-direction: column;
			padding: 10px !important;
		}

		.wpforo-post {
			blockquote {
				width: 100% !important;
				border: none !important;
				background: #282628 !important;
			}

			.wpf-embed {
				margin: auto;
			}

			figure[data-attachids] {
				margin: auto;
			}
		}
		.wpforo-revision-body {
			blockquote {
				width: 100% !important;
				border: none !important;
				background: #282628 !important;
			}
		}

		.wpf-spoiler-wrap {
			.wpf-spoiler-head {
				border-left: none !important;
			}

			.wpf-spoiler-body {
				border-top: 1px solid #646464 !important;
				border-left: none !important;
			}
		}

		.wpf-head-bar {
			border-radius: 0 !important;
		}

		.wpf-button-secondary {
			box-shadow: none !important;
		}

		.wpf-button-outlined {
			box-shadow: none !important;
		}

		#wpforo-stat-body {
			.wpf-last-info {
				div {
					&.wpf-forum-icons {
						border-top: 1px solid #585858 !important;
					}
				}
			}
		}

		.wpforo-profile {
			.wpforo-profile-menu {
				.wpf-profile-menu {
					&:hover {
						color: #e0e0e0 !important;
					}
				}
			}
		}

		.wpforo-members {
			gap: 1.5rem;
			padding-left: 0 !important;

			.wpforo-member {
				width: calc(33% - 0.75rem) !important;
				margin: 0 !important;
				padding: 1rem !important;
				border-radius: 1rem;
				background: #1f1f1f !important;

				@include media-breakpoint-down(lg) {
					width: calc(50% - 0.75rem) !important;
				}

				@include media-breakpoint-down(md) {
					width: 100% !important;
				}

				.wpforo-member-bg {
					width: calc(100% - 2rem) !important;
				}
			}
		}
	}
}
